package com.alphastack.portfolio.components

import androidx.compose.runtime.Composable
import com.alphastack.portfolio.theme.AlphaTheme
import com.alphastack.portfolio.util.isMobile
import com.varabyte.kobweb.compose.css.Height
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.H1
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun HeaderSection(
    modifier: Modifier = Modifier
) {
    val breakpoint = rememberBreakpoint()
    val padding = if (breakpoint.isMobile()) 30.px else 100.px

    Box(
        modifier = modifier
            .fillMaxSize()
            .maxWidth(AlphaTheme.MAX_WIDTH)
            .height(Height.FitContent)
            .padding(padding)
            .position(Position.Relative),
    ) {
        val horizontalAlignment = if (breakpoint <= Breakpoint.SM) Alignment.CenterHorizontally else Alignment.Start

        Column(
            verticalArrangement = Arrangement.Top,
            horizontalAlignment = horizontalAlignment,
            modifier = Modifier
                .fillMaxWidth(),
        ) {
            HeaderTitle()
            HeaderSubTitle()

            /*AlphaButton(
                text = "Download CV",
                onClick = {},
                modifier = Modifier
                    .margin(top = 40.px)
            )*/
        }

        HeaderImage()
    }
}

@Composable
private fun HeaderSubTitle() {
    val breakpoint = rememberBreakpoint()
    val subtitleFontSize = if (breakpoint <= Breakpoint.SM) 16.px else 20.px
    val marginTop = if (breakpoint <= Breakpoint.SM) 8.px else 12.px

    P(
        attrs = Modifier
            .then(AlphaTheme.typography.latoNormal)
            .color(AlphaTheme.colors.gray50)
            .fontSize(subtitleFontSize)
            .margin(top = marginTop)
            .toAttrs()
    ) {
        Text("I build Android applications.")
    }
}

@Composable
private fun HeaderTitle() {
    val breakpoint = rememberBreakpoint()
    val titleFontSize = if (breakpoint <= Breakpoint.SM) 40.px else 70.px
    val topMargin = if (breakpoint <= Breakpoint.SM) 10.px else 20.px

    H1(
        attrs = Modifier
            .then(AlphaTheme.typography.latoBlack)
            .color(AlphaTheme.colors.gray50)
            .fontSize(titleFontSize)
            .toAttrs()
    ) {
        Text("I'm")
    }
    H1(
        attrs = Modifier
            .then(AlphaTheme.typography.latoBlack)
            .color(AlphaTheme.colors.primary)
            .fontSize(titleFontSize)
            .margin(top = -(topMargin))
            .toAttrs()
    ) {
        Text("Dhaval Shah")
    }
}

@Composable
private fun HeaderImage() {
    val breakpoint = rememberBreakpoint()
    val imageWidth = if (breakpoint >= Breakpoint.LG) 350.px else 300.px
    val isImageVisible = breakpoint >= Breakpoint.MD

    if (isImageVisible) {
        Image(
            src = "background.svg",
            modifier = Modifier
                .width(imageWidth)
                .position(Position.Absolute)
                .bottom(0.px)
                .right(0.px)
        )
    }
}
