@Composable
fun ProjectsSection(
    modifier: Modifier = Modifier,
    sectionId: String
) {
    val breakpoint = rememberBreakpoint()
    val padding = if (breakpoint.isMobile()) 30.px else 60.px

    Column(
        horizontalAlignment = Alignment.Start,
        modifier = modifier
            .id(sectionId)
            .fillMaxWidth()
            .maxWidth(AlphaTheme.MAX_WIDTH)
            .padding(padding)
            .backgroundColor(AlphaTheme.colors.white)
    ) {
        SectionHeader(
            title = "What I have created?",
            textColor = AlphaTheme.colors.gray50,
            modifier = Modifier.fillMaxWidth()
        )

        for ((index, project) in PROJECTS.withIndex()) {
            val reversed = index % 2 == 1
            val isFirst = index == 0
            ProjectComponent(
                imageSrc = project.imageSrc,
                projectTitle = project.projectTitle,
                projectDescription = project.projectDescription,
                buttonText = if (project.externalUrl.isNotEmpty()) "Open in play store" else "",
                onClick = { openExternalUrl(project.externalUrl) },
                reversed = reversed,
                isFirst = isFirst,
                tags = project.tags,
                organisation = project.organisation,
            )
        }
    }
}

private fun openExternalUrl(url: String) {
    window.open(
        href = url,
        strategy = OpenLinkStrategy.IN_NEW_TAB
    )
}
