package com.alphastack.portfolio.components

import androidx.compose.runtime.Composable
import com.alphastack.portfolio.sections.Organisation
import com.alphastack.portfolio.theme.AlphaTheme
import com.alphastack.portfolio.util.isMobile
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.H2
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun ProjectComponent(
    modifier: Modifier = Modifier,
    imageSrc: String,
    projectTitle: String,
    projectDescription: String,
    buttonText: String,
    tags: List<String>,
    onClick: () -> Unit,
    reversed: Boolean = false,
    isFirst: Boolean = false,
    organisation: Organisation,
) {
    val breakpoint = rememberBreakpoint()
    if (breakpoint.isMobile()) {
        Column(
            horizontalAlignment = Alignment.CenterHorizontally,
            modifier = modifier
                .margin(top = if (isFirst) 0.px else 30.px)
                .gap(30.px),
        ) {
            ProjectImage(imageSrc)
            ProjectContent(projectTitle, projectDescription, buttonText, tags, onClick, organisation)
        }
    } else {
        Row(
            modifier = modifier
                .margin(top = 60.px)
                .gap(60.px),
        ) {
            if (!reversed) {
                ProjectImage(imageSrc)
                ProjectContent(projectTitle, projectDescription, buttonText, tags, onClick, organisation)
            } else {
                ProjectContent(projectTitle, projectDescription, buttonText, tags, onClick, organisation)
                ProjectImage(imageSrc)
            }
        }
    }
}

@Composable
private fun ProjectContent(
    projectTitle: String,
    projectDescription: String,
    buttonText: String,
    tags: List<String>,
    onClick: () -> Unit,
    organisation: Organisation,
) {
    val breakpoint = rememberBreakpoint()
    val alignment = if (breakpoint.isMobile()) {
        Alignment.CenterHorizontally
    } else {
        Alignment.Start
    }

    Column(
        verticalArrangement = Arrangement.Center,
        horizontalAlignment = alignment,
        modifier = Modifier
            .fillMaxHeight()
    ) {
        ProjectTitle(projectTitle, organisation)
        ProjectDescription(projectDescription)

        if (tags.isNotEmpty()) {
            ProjectTags(
                tags = tags,
                modifier = Modifier
                    .margin(top = 15.px)
            )
        }

        if (buttonText.isNotBlank()) {
            AlphaButton(
                text = buttonText,
                onClick = { onClick() },
                modifier = Modifier
                    .margin(top = 20.px)
            )
        }
    }
}

@Composable
private fun ProjectDescription(projectDescription: String) {
    val breakpoint = rememberBreakpoint()
    val fontSize = if (breakpoint.isMobile()) {
        16.px
    } else {
        20.px
    }
    val textAlign = if (breakpoint.isMobile()) TextAlign.Center else TextAlign.Start

    P(
        attrs = Modifier
            .then(AlphaTheme.typography.latoNormal)
            .fontSize(fontSize)
            .color(AlphaTheme.colors.gray50)
            .margin(top = 10.px)
            .textAlign(textAlign)
            .toAttrs()
    ) {
        Text(projectDescription)
    }
}

@Composable
private fun ProjectTitle(projectTitle: String, organisation: Organisation) {
    val breakpoint = rememberBreakpoint()
    val fontSize = if (breakpoint < Breakpoint.MD) {
        24.px
    } else {
        32.px
    }

    H2(
        attrs = Modifier
            .then(AlphaTheme.typography.latoNormal)
            .fontSize(fontSize)
            .color(AlphaTheme.colors.gray50)
            .toAttrs()
    ) {
        Text(projectTitle)
    }
}

@Composable
private fun ProjectImage(imageSrc: String) {
    val breakpoint = rememberBreakpoint()
    val widthModifier = if (breakpoint < Breakpoint.MD) {
        Modifier.width(200.px)
    } else {
        Modifier
    }

    Image(
        src = imageSrc,
        modifier = Modifier
            .then(widthModifier)
    )
}
