package com.alphastack.portfolio.pages

import androidx.compose.runtime.*
import com.alphastack.portfolio.components.HeaderSection
import com.alphastack.portfolio.components.NavigationBar
import com.alphastack.portfolio.sections.AboutSection
import com.alphastack.portfolio.sections.ContactSection
import com.alphastack.portfolio.sections.ProjectsSection
import com.alphastack.portfolio.theme.AlphaTheme
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.core.Page

const val SECTION_ID_CONTACT = "contact"
const val SECTION_ID_PROJECTS = "projects"
const val SECTION_ID_ABOUT = "about"

@Page
@Composable
fun HomePage() {
    Column (
        horizontalAlignment = Alignment.CenterHorizontally,
        modifier = Modifier
            .fillMaxSize()
            .backgroundColor(AlphaTheme.colors.background)
    ) {
        NavigationBar()
        HeaderSection()
        AboutSection(sectionId = SECTION_ID_ABOUT)
        ProjectsSection(sectionId = SECTION_ID_PROJECTS)
        ContactSection(sectionId = SECTION_ID_CONTACT)
    }
}
