package com.alphastack.portfolio.components

import androidx.compose.runtime.Composable
import com.alphastack.portfolio.theme.AlphaTheme
import com.varabyte.kobweb.compose.css.Cursor
import com.varabyte.kobweb.compose.css.UserSelect
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.style.*
import com.varabyte.kobweb.silk.theme.shapes.RectF
import com.varabyte.kobweb.silk.theme.shapes.clip
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.A
import org.jetbrains.compose.web.dom.Text

@Composable
fun AlphaButton(
    modifier: Modifier = Modifier,
    text: String,
    onClick: () -> Unit,
) {
    A(
        attrs = AlphaButtonStyle
            .toModifier()
            .onClick { onClick() }
            .then(modifier)
            .toAttrs()
    ) {
        Text(text)
    }
}

val AlphaButtonStyle by ComponentStyle {
    base {
        Modifier
            .clip(RectF(4.px))
            .padding(topBottom = 8.px, leftRight = 16.px)
            .backgroundColor(AlphaTheme.colors.white)
            .color(AlphaTheme.colors.gray30)
            .then(AlphaTheme.typography.latoNormal)
            .fontSize(14.px)
            .border(
                width = 1.px,
                color = AlphaTheme.colors.gray30,
                style = LineStyle.Solid,
            )
            .borderRadius(4.px)
            .cursor(Cursor.Pointer)
            .userSelect(UserSelect.None)
    }
    hover {
        Modifier
            .backgroundColor(AlphaTheme.colors.gray30)
            .color(AlphaTheme.colors.white)
    }
}
