import org.jetbrains.compose.web.css.px

private const val GITHUB_URL = "https://github.com/dhavalshah05"
private const val TWITTER_URL = "https://twitter.com/_shahdhaval"
private const val LINKEDIN_URL = "https://www.linkedin.com/in/dhavalshah53/"

@Composable
fun ContactSection(
    modifier: Modifier = Modifier,
    sectionId: String
) {
    Box(
        modifier = modifier
            .id(sectionId)
            .fillMaxWidth()
            .backgroundColor(AlphaTheme.colors.backgroundFooter),
        contentAlignment = Alignment.Center
    ) {
        val breakpoint = rememberBreakpoint()
        val isMobileLayout = breakpoint <= Breakpoint.SM

        if (isMobileLayout) {
            Column (
                horizontalAlignment = Alignment.CenterHorizontally,
                modifier = Modifier
                    .fillMaxWidth()
                    .maxWidth(AlphaTheme.MAX_WIDTH)
                    .padding(30.px)
                    .gap(30.px)
            ) {
                SectionHeader(
                    title = "Where to contact me?",
                    textColor = AlphaTheme.colors.gray50
                )

                ContactIcons()
            }
        } else {
            Row(
                verticalAlignment = Alignment.CenterVertically,
                modifier = Modifier
                    .fillMaxWidth()
                    .maxWidth(AlphaTheme.MAX_WIDTH)
                    .padding(60.px)
            ) {
                SectionHeader(
                    title = "Where to contact me?",
                    textColor = AlphaTheme.colors.gray50
                )

                Spacer()

                ContactIcons()
            }
        }
    }

}

@Composable
private fun ContactIcons() {
    Row(
        modifier = Modifier
            .gap(20.px)
    ) {
        ContactIcon(
            src = "icons/icon-github.svg",
            onClick = { window.openUrlInNewTab(GITHUB_URL) }
        )
        ContactIcon(
            src = "icons/icon-twitter.svg",
            onClick = { window.openUrlInNewTab(TWITTER_URL) }
        )
        ContactIcon(
            src = "icons/icon-linkedin.svg",
            onClick = { window.openUrlInNewTab(LINKEDIN_URL) }
        )
        /*ContactIcon(
            src = "icons/icon-mail.svg",
            onClick = {}
        )*/
    }
}

@Composable
private fun ContactIcon(
    src: String,
    onClick: () -> Unit
) {
    val breakpoint = rememberBreakpoint()
    val size = if (breakpoint <= Breakpoint.MD) 40.px else 60.px

    Image(
        src = src,
        modifier = ContactIconStyle
            .toModifier()
            .size(size)
            .onClick { onClick() }
    )
}

val ContactIconStyle by ComponentStyle {
    base {
        Modifier
            .cursor(Cursor.Pointer)
            .transition(
                transitions = arrayOf(
                    CSSTransition(
                        property = "scale",
                        duration = 300.ms
                    )
                )
            )
    }

    hover {
        Modifier
            .scale(1.1f)
    }
}
