package com.alphastack.portfolio.components

import androidx.compose.runtime.Composable
import com.alphastack.portfolio.theme.AlphaTheme
import com.varabyte.kobweb.compose.css.UserSelect
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.theme.shapes.Rect
import com.varabyte.kobweb.silk.theme.shapes.clip
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Text

@Composable
fun ProjectTags(
    modifier: Modifier = Modifier,
    tags: List<String>,
) {
    Row(
        modifier = modifier
            .gap(15.px)
    ) {
        tags.forEach { ProjectTag(text = it) }
    }
}

@Composable
private fun ProjectTag(
    modifier: Modifier = Modifier,
    text: String,
) {
    Box(
        modifier = modifier
            .clip(Rect(4.px))
            .fontSize(12.px)
            .backgroundColor(AlphaTheme.colors.grayText80)
            .color(AlphaTheme.colors.gray50)
            .padding(leftRight = 8.px, topBottom = 4.px)
            .userSelect(UserSelect.None)
            .then(AlphaTheme.typography.latoNormal)
    ) {
        Text(text)
    }
}
