package com.alphastack.portfolio.components

import androidx.compose.runtime.Composable
import com.alphastack.portfolio.theme.AlphaTheme
import com.alphastack.portfolio.util.isMobile
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.textAlign
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.jetbrains.compose.web.css.CSSColorValue
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.H2
import org.jetbrains.compose.web.dom.Text

@Composable
fun SectionHeader(
    modifier: Modifier = Modifier,
    title: String,
    textColor: CSSColorValue = AlphaTheme.colors.grayText,
) {
    val breakpoint = rememberBreakpoint()
    val fontSize = if (breakpoint <= Breakpoint.MD) 24.px else 32.px
    val textAlign = if (breakpoint.isMobile()) TextAlign.Center else TextAlign.Start

    H2(
        attrs = modifier
            .then(AlphaTheme.typography.latoBold)
            .fontSize(fontSize)
            .color(textColor)
            .textAlign(textAlign)
            .toAttrs()
    ) {
        Text(title.uppercase())
    }
}
