package com.alphastack.portfolio.sections

import androidx.compose.runtime.Composable
import com.alphastack.portfolio.components.SectionHeader
import com.alphastack.portfolio.theme.AlphaTheme
import com.alphastack.portfolio.util.isMobile
import com.varabyte.kobweb.compose.css.Cursor
import com.varabyte.kobweb.compose.css.LineHeight
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.navigation.OpenLinkStrategy
import com.varabyte.kobweb.navigation.open
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.hover
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import kotlinx.browser.window
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun AboutSection(
    modifier: Modifier = Modifier,
    sectionId: String
) {
    val breakpoint = rememberBreakpoint()
    val sectionPadding = if (breakpoint.isMobile()) 30.px else 60.px
    val descriptionFontSize = if (breakpoint.isMobile()) 16.px else 18.px
    val descriptionMarginTop = if (breakpoint.isMobile()) 20.px else 30.px
    val descriptionTextAlign = if (breakpoint.isMobile()) TextAlign.Center else TextAlign.Start

    Box(
        modifier = modifier
            .id(sectionId)
            .fillMaxWidth()
            .backgroundColor(AlphaTheme.colors.gray50),
        contentAlignment = Alignment.Center
    ) {
        Column(
            modifier = Modifier
                .fillMaxWidth()
                .maxWidth(AlphaTheme.MAX_WIDTH)
                .padding(sectionPadding)
        ) {
            SectionHeader(
                title = "What I do?",
                modifier = Modifier.fillMaxWidth()
            )
            P(
                attrs = Modifier
                    .then(AlphaTheme.typography.latoNormal)
                    .fontSize(descriptionFontSize)
                    .margin(top = descriptionMarginTop)
                    .color(AlphaTheme.colors.grayText80)
                    .letterSpacing(0.4.px)
                    .lineHeight(LineHeight.Inherit)
                    .textAlign(descriptionTextAlign)
                    .toAttrs()
            ) {
                Text("I am an application developer having 6+ years of experience, who build apps which are simple in terms of UX and rich in terms of UI and functionality.  Currently, I am focused on building beautiful apps using ")
                SpanText(
                    text = "Jetpack Compose.",
                    modifier = JetpackComposeStyle
                        .toModifier()
                        .onClick { openJetpackComposeWebsite() }
                )
            }
        }
    }
}

val JetpackComposeStyle by ComponentStyle {
    base {
        Modifier
            .color(AlphaTheme.colors.orange)
            .cursor(Cursor.Pointer)
            .borderBottom(
                width = 1.px,
                style = LineStyle.None,
                color = AlphaTheme.colors.orange
            )
    }
    hover {
        Modifier
            .color(AlphaTheme.colors.orange)
            .cursor(Cursor.Pointer)
            .borderBottom(
                width = 1.px,
                style = LineStyle.Solid,
                color = AlphaTheme.colors.orange
            )
    }
}

private fun openJetpackComposeWebsite() {
    window.open(
        href = "https://developer.android.com/jetpack/compose",
        strategy = OpenLinkStrategy.IN_NEW_TAB
    )
}
