package com.alphastack.portfolio.components

import androidx.compose.runtime.Composable
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import com.alphastack.portfolio.pages.SECTION_ID_ABOUT
import com.alphastack.portfolio.pages.SECTION_ID_CONTACT
import com.alphastack.portfolio.pages.SECTION_ID_PROJECTS
import com.alphastack.portfolio.theme.AlphaTheme
import com.alphastack.portfolio.util.smoothScrollToElement
import com.varabyte.kobweb.compose.css.TextDecorationLine
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.attrsModifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.thenIf
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.style.*
import com.varabyte.kobweb.silk.theme.shapes.Rect
import com.varabyte.kobweb.silk.theme.shapes.clip
import kotlinx.browser.window
import org.jetbrains.compose.web.css.Position
import org.jetbrains.compose.web.css.px

@Composable
fun NavigationBar(
    modifier: Modifier = Modifier,
) {
    val selectedItem = remember { mutableStateOf("About") }
    val navbarHeight = remember { mutableStateOf(0) }

    Box(
        contentAlignment = Alignment.Center,
        modifier = modifier
            .fillMaxWidth()
            .backgroundColor(AlphaTheme.colors.background)
            .zIndex(100)
            .position(Position.Sticky)
            .top(0.px)
            .left(0.px)
            .right(0.px)
    ) {
        Row(
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.Center,
            modifier = modifier
                .fillMaxWidth()
                .maxWidth(AlphaTheme.MAX_WIDTH)
                .padding(topBottom = 30.px)
                .gap(30.px)
                .attrsModifier {
                    ref {
                        navbarHeight.value = it.clientHeight
                        onDispose {  }
                    }
                }
        ) {
            NavigationBarItem(
                path = "",
                text = "About".uppercase(),
                selected = selectedItem.value == "About",
                onClick = {
                    selectedItem.value = "About"
                    window.smoothScrollToElement(SECTION_ID_ABOUT, navbarHeight.value)
                }
            )
            NavigationBarItem(
                path = "",
                text = "Projects".uppercase(),
                selected = selectedItem.value == "Projects",
                onClick = {
                    selectedItem.value = "Projects"
                    window.smoothScrollToElement(SECTION_ID_PROJECTS, navbarHeight.value)
                }
            )
            NavigationBarItem(
                path = "",
                text = "Contact".uppercase(),
                selected = selectedItem.value == "Contact",
                onClick = {
                    selectedItem.value = "Contact"
                    window.smoothScrollToElement(SECTION_ID_CONTACT, navbarHeight.value)
                }
            )
        }
    }
}

@Composable
private fun NavigationBarItem(
    path: String,
    text: String,
    selected: Boolean = false,
    onClick: () -> Unit
) {
    Link(
        path = path,
        text = text.uppercase(),
        modifier = NavigationBarItemStyle
            .toModifier()
            .onClick { onClick.invoke() }
            .thenIf(selected, NavigationBarItemStyleSelected.toModifier())
    )
}

val NavigationBarItemStyle by ComponentStyle {
    base {
        Modifier
            .padding(leftRight = 10.px, topBottom = 4.px)
            .backgroundColor(AlphaTheme.colors.white)
            .color(AlphaTheme.colors.primary)
            .clip(Rect(4.px))
            .then(AlphaTheme.typography.latoNormal)
            .fontSize(16.px)
    }
    anyLink {
        Modifier
            .textDecorationLine(TextDecorationLine.None)
            .color(AlphaTheme.colors.primary)
    }
    hover {
        Modifier
            .backgroundColor(AlphaTheme.colors.primary)
            .color(AlphaTheme.colors.white)
    }
}

val NavigationBarItemStyleSelected by ComponentStyle {
    base {
        Modifier
            .backgroundColor(AlphaTheme.colors.primary)
            .color(AlphaTheme.colors.white)
    }
    anyLink {
        Modifier
            .textDecorationLine(TextDecorationLine.None)
            .color(AlphaTheme.colors.white)
    }
}
